<template>
  <div>
    <v-card-title class="text-h5" style="color: #273746"> </v-card-title>

    <v-card-text>
      <div>
        <v-expansion-panels accordion v-model="panel">
          <v-expansion-panel v-for="(item, i) in itemsT" :key="i">
            <v-expansion-panel-header
              >{{ item.number }}) {{ item.specificPoint }}
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <div>
                <v-card-text>
                  <div class="text-right">
                    <v-btn icon @click="openDeleteModal(item)">
                      <v-icon>delete</v-icon>
                    </v-btn>
                  </div>
                  <v-text-field
                    v-model="item.specificPoint"
                    outlined
                    label="Punto específico"
                    required
                    @change="sentChangeItems"
                  ></v-text-field>

                  <div v-for="(itemLabel, key, index) in item.items" :key="index">
                    <v-checkbox
                      v-model="itemLabel.value"
                      :label="`${itemLabel.label}`"
                    ></v-checkbox>
                  </div>

                  <v-textarea
                    v-model="item.specs"
                    label="Comentarios"
                    auto-grow
                    outlined
                    name="input-7-4"
                    @watch="sentChangeItems"
                  ></v-textarea>

                  <div>
                    <br />
                  </div>
                </v-card-text>
              </div>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </div>
    </v-card-text>
    <div class="text-center">
      <v-btn rounded color="primary" dark @click="addPoint()">
        Nuevo punto especifico
      </v-btn>
    </div>
    <br />

    <v-dialog v-model="dialog" persistent max-width="290">
      <v-card>
        <v-card-title class="text-h5"> Borrar </v-card-title>
        <v-card-text>
          ¿Seguro que desea borrar?, Se ajustará el contador.</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="#273746" text @click="dialog = false"> Cancelar </v-btn>
          <v-btn outlined color="#273746" @click="deleteItem(idItemDelete)">
            Si, Borrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "ItemArea",
  props: {
    template: Object,
  },
  data() {
    return {
      idItemDelete: null,
      dialog: false,
      itemsT: [],
      description: null,
      nom: null,
      value: null,
      items: null,
      model: null,
      panel: null,
    };
  },
  watch: {
    value(e) {
      this.$emit("setOption", e);
    },
    /*  itemsT(e) {
      console.log(e);
      this.$emit("setItemsT", e);
    }, */
    itemsT: {
      deep: true,
      handler() {
        console.log(this.itemsT);
        this.$emit("setItemsT", this.itemsT);
      },
    },
    template() {
      this.start();
    },
  },
  created() {
    this.start();
  },
  methods: {
    start() {
      console.log("datps");
      console.log(this.template);
      this.description = this.template.description;
      this.nom = this.template.nom;
      this.value = this.template.value;
      this.items = this.template.items;
      this.model = this.template.model;
      if (this.template.itemsT) {
        this.itemsT = this.template.itemsT;
      }
    },
    addPoint() {
      const end = this.itemsT.length + 1;
      this.panel = this.itemsT.length;
      const newPoint = {
        number: end,
        specificPoint: "",
        specs: "",
        value: null,
        model: this.model,
        items: JSON.parse(JSON.stringify(this.items)),
      };
      this.itemsT.push(newPoint);
    },
    show() {
      console.log(this.itemsT);
      this.$emit("setItemsT", this.itemsT);
    },
    sentChangeItems() {
      console.log("change");
      //this.$emit("setItemsT", this.itemsT);
    },
    openDeleteModal(item) {
      this.idItemDelete = item;
      this.dialog = true;
      /*    console.log("el item");
      console.log(item); */
    },
    deleteItem(getItem) {
      let temp = this.itemsT.filter((item) => item.number != getItem.number);
      console.log(temp);
      if (temp.length > 0) {
        let count = 0;
        let accommodate = temp.map((item) => {
          count++;
          return {
            number: count,
            specificPoint: item.specificPoint,
            specs: item.specs,
            value: null,
            model: "Tierra abierta",
          };
        });
        this.itemsT = accommodate;
        console.log("accommodate");
        console.log(accommodate);
      }
      if (temp.length == 0) {
        this.itemsT = [];
      }
      this.panel = null;
      this.dialog = false;

      //const result = words.filter(word => word.length > 6);
    },

    /*  change() {
      Soon
      this.$emit("setOption", this.value);
      Father
      @setOption="(value) => (item.value = value)"
    }, */
  },
};
</script>

<style>
.v-chip.v-size--default {
  border-radius: 20px;
  font-size: 14px;
  height: 42px;
}
</style>